
import {defineComponent} from 'vue';

export default defineComponent({
  name: 'Tabs',
  props: {
    value: {
      type: String
    },
    classPrefix: {
      type: String
    },
    dataSource: {
      type: Array
    }
  },
  setup(props, context) {
    const select = (data: string) => {
      if (data !== props.value) {
        context.emit('update:value', data);
      }
    };
    return {select};
  }
});
